// @flow weak

import React, {
  PureComponent
}                     from 'react';
// import PropTypes      from 'prop-types';
import AnimatedView   from '../../components/animatedView/AnimatedView';
import Validation from 'react-validation';
import validator from 'validator';
import validation from '../../components/validate.jsx';

class Registrarse extends PureComponent {
  constructor(props) {
    super(props)
    this.registro = this.registro.bind(this);
  }
  
  render() {
    return(
      <AnimatedView>
        <h1>
          Registrate totalmente gratis
        </h1>
        <Validation.components.Form onSubmit={this.registro.bind(this)}>    
        <div className="form-group">
        <Validation.components.Input type="text" value='' validations={['required', 'email']} placeholder="Ingresa tu correo electronico" name='form-email' className="form-control" id="form-email" />
        </div>
        <button type="submit" className="mybtn">Registrarme </button>
        </Validation.components.Form>
      </AnimatedView>
    );
  }

  registro(event){
    event.preventDefault();     

  }
}

export default Registrarse;



